/**
 * Kakao ÏÜåÏÖú Î°úÍ∑∏Ïù∏ Íµ¨ÌòÑ
 * 
 * Ïù¥ ÌååÏùºÏùÄ Kakao OAuthÎ•º ÌÜµÌïú Ïù∏Ï¶ù Íµ¨ÌòÑÏùÑ Îã¥ÎãπÌï©ÎãàÎã§.
 */

import { SupabaseClient } from '@supabase/supabase-js';
import { Database } from '@/types/supabase';
import { 
  SocialAuthOptions, 
  AuthResult,
  OAuthProviderConfig,
  SocialAuthError,
  SocialAuthErrorCode
} from './types';

/**
 * Kakao OAuth ÏÑ§Ï†ï
 */
export function getKakaoConfig(): OAuthProviderConfig {
  return {
    clientId: process.env.NEXT_PUBLIC_KAKAO_CLIENT_ID || '',
    clientSecretEnvKey: 'KAKAO_CLIENT_SECRET',
    defaultScopes: [
      'account_email',
      'profile_image', 
      'profile_nickname'
    ],
    additionalConfig: {
      // Kakao ÌäπÌôî ÏÑ§Ï†ï
      prompt: 'login consent',
      // Kakao JavaScript SDK Ïï± ÌÇ§ (ÏõπÏö©)
      jsAppKey: process.env.NEXT_PUBLIC_KAKAO_JS_APP_KEY || process.env.NEXT_PUBLIC_KAKAO_CLIENT_ID || '',
      // Kakao REST API ÌÇ§ (ÏÑúÎ≤ÑÏö©)
      restApiKey: process.env.KAKAO_REST_API_KEY || '',
      // Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ Ïãú ÏûêÎèô Ïó∞Í≤∞ Ïó¨Î∂Ä
      autoConnect: true
    }
  };
}

// Î™®Îì† ÌôòÍ≤ΩÏóêÏÑú ÎèôÏùº Ï≤òÎ¶¨: ÌôòÍ≤ΩÎ≥Ñ ÌäπÎ≥Ñ Î°úÏßÅ Ï†úÍ±∞

/**
 * Kakao Î°úÍ∑∏Ïù∏ Íµ¨ÌòÑ
 * 
 * @param supabase Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏
 * @param options Ïù∏Ï¶ù ÏòµÏÖò
 * @returns Ïù∏Ï¶ù Í≤∞Í≥º
 */
export async function signInWithKakaoImpl(
  supabase: SupabaseClient<Database>,
  options?: SocialAuthOptions
): Promise<AuthResult> {
  try {
    // ÏÑ§Ï†ïÍ∞í Ï§ÄÎπÑ
    const config = getKakaoConfig();
    const scopes = options?.scopes || config.defaultScopes;
    
    // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Î¶¨Îã§Ïù¥Î†âÌä∏ URL Ï†ÄÏû• (ÏΩúÎ∞± ÌõÑ ÎêòÎèåÏïÑÏò¨ ÏúÑÏπò)
    if (typeof localStorage !== 'undefined') {
      const returnUrl = options?.additionalParams?.return_url || window.location.pathname;
      localStorage.setItem('auth_return_url', returnUrl);
    }
    
    // Kakao ÌäπÌôî ÌååÎùºÎØ∏ÌÑ∞
    const kakaoParams: Record<string, string> = {
      prompt: (config.additionalConfig as any)?.prompt || 'login consent',
      ...options?.additionalParams
    };
    
    // service_termsÍ∞Ä Ï°¥Ïû¨ÌïòÎäî Í≤ΩÏö∞ÏóêÎßå Ï∂îÍ∞Ä
    if (options?.additionalParams?.service_terms) {
      kakaoParams.service_terms = options.additionalParams.service_terms;
    }
    
    // Ï§ëÎ≥µ Ï†úÍ±∞Îêú Í≥†Ïú† scope ÏÉùÏÑ± Î∞è ÎîîÎ≤ÑÍπÖ
    const uniqueScopes = Array.from(new Set(scopes));
    const finalScopeString = uniqueScopes.join(' ');
    
    // ÌòÑÏû¨ ÎèÑÎ©îÏù∏ Í∏∞Î∞ò ÏΩúÎ∞± URL ÏÉùÏÑ± (Î™®Îì† ÌôòÍ≤Ω ÎèôÏùº)
    const redirectUrl = typeof window !== 'undefined' 
      ? `${window.location.origin}/auth/callback/kakao`
      : options?.redirectUrl;
    
    // ÎîîÎ≤ÑÍπÖ: OAuth ÏÑ§Ï†ï ÌôïÏù∏
    console.log('üîç Kakao OAuth Debug:', {
      redirectUrl,
      supabaseUrl: process.env.NEXT_PUBLIC_SUPABASE_URL,
      originalScopes: scopes,
      uniqueScopes: uniqueScopes,
      finalScopeString: finalScopeString
    });
    
    const clientId = config.clientId;
    if (!clientId) {
      throw new Error('Kakao Client IDÍ∞Ä ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
    }
    
    const targetRedirectUrl = redirectUrl || `${process.env.NEXT_PUBLIC_APP_URL || 'https://www.picnic.fan'}/auth/callback/kakao`;
    
    console.log('üöÄ Kakao OAuth ÏãúÎèÑ:', {
      targetRedirectUrl,
      origin: typeof window !== 'undefined' ? window.location.origin : 'unknown'
    });
    
    console.log('üöÄ Kakao OAuth ÏãúÎèÑ (Supabase ÌëúÏ§Ä):', {
      redirectUri: targetRedirectUrl,
      scopes: finalScopeString
    });
    
    // üéØ Supabase ÌëúÏ§Ä OAuth ÏÇ¨Ïö© (PKCE ÏûêÎèô Ï≤òÎ¶¨)
    const { data, error } = await supabase.auth.signInWithOAuth({
      provider: 'kakao',
      options: {
        redirectTo: targetRedirectUrl,
        scopes: finalScopeString
      }
    });
    
    if (error) {
      console.error('‚ùå Supabase Kakao OAuth Ïò§Î•ò:', error);
      throw new SocialAuthError(
        SocialAuthErrorCode.AUTH_PROCESS_FAILED,
        `Supabase Kakao OAuth Ïã§Ìå®: ${error.message}`,
        'kakao',
        error
      );
    }
    
    console.log('‚úÖ Kakao OAuth Î¶¨ÎîîÎ†âÏÖò ÏÑ±Í≥µ:', data);
    
    return {
      success: true,
      provider: 'kakao',
      message: 'Kakao Î°úÍ∑∏Ïù∏ Î¶¨ÎîîÎ†âÏÖò Ï§ë...'
    };
    
  } catch (error) {
    if (error instanceof SocialAuthError) {
      throw error;
    }
    
    throw new SocialAuthError(
      SocialAuthErrorCode.AUTH_PROCESS_FAILED,
      error instanceof Error ? error.message : 'Ïïå Ïàò ÏóÜÎäî Kakao Î°úÍ∑∏Ïù∏ Ïò§Î•ò',
      'kakao',
      error
    );
  }
}

/**
 * Kakao ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥ Ï†ïÍ∑úÌôî
 * 
 * @param profile Kakao APIÏóêÏÑú Î∞òÌôòÎêú ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥
 * @returns ÌëúÏ§ÄÌôîÎêú ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥
 */
export function normalizeKakaoProfile(profile: any): Record<string, any> {
  // Kakao ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥Îäî Ï£ºÎ°ú propertiesÏôÄ kakao_account Í∞ùÏ≤¥Ïóê Ìè¨Ìï®ÎêòÏñ¥ ÏûàÏùå
  const kakaoAccount = profile.kakao_account || {};
  const properties = profile.properties || {};
  
  return {
    id: profile.id?.toString() || '',
    name: properties.nickname || '',
    email: kakaoAccount.email || '',
    emailVerified: kakaoAccount.is_email_verified || false,
    avatar: properties.profile_image || properties.thumbnail_image || '',
    gender: kakaoAccount.gender || null,
    birthday: kakaoAccount.birthday || null,
    ageRange: kakaoAccount.age_range || null,
    provider: 'kakao',
    rawProfile: {
      nickname: properties.nickname,
      profileImage: properties.profile_image,
      thumbnailImage: properties.thumbnail_image
    }
  };
}

/**
 * Ïï°ÏÑ∏Ïä§ ÌÜ†ÌÅ∞ÏúºÎ°ú Kakao ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
 * (ÏÑúÎ≤Ñ Ï∏°ÏóêÏÑú ÏÇ¨Ïö©ÌïòÎäî Ìï®Ïàò)
 * 
 * @param accessToken Kakao Ïï°ÏÑ∏Ïä§ ÌÜ†ÌÅ∞
 * @returns Kakao ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥
 */
export async function getKakaoUserInfo(accessToken: string): Promise<Record<string, any>> {
  try {
    const response = await fetch('https://kapi.kakao.com/v2/user/me', {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${accessToken}`,
        'Content-Type': 'application/json'
      }
    });
    
    if (!response.ok) {
      throw new Error(`Kakao API ÏöîÏ≤≠ Ïã§Ìå®: ${response.status} ${response.statusText}`);
    }
    
    const userData = await response.json();
    return userData;
  } catch (error) {
    console.error('Kakao ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå®:', error);
    throw new SocialAuthError(
      SocialAuthErrorCode.PROFILE_FETCH_FAILED,
      'Ïπ¥Ïπ¥Ïò§ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.',
      'kakao',
      error
    );
  }
}

/**
 * Kakao Ïï°ÏÑ∏Ïä§ ÌÜ†ÌÅ∞ Í∞±Ïã†
 * (ÏÑúÎ≤Ñ Ï∏°ÏóêÏÑú ÏÇ¨Ïö©ÌïòÎäî Ìï®Ïàò)
 * 
 * @param refreshToken Kakao Î¶¨ÌîÑÎ†àÏãú ÌÜ†ÌÅ∞
 * @returns ÏÉàÎ°úÏö¥ ÌÜ†ÌÅ∞ Ï†ïÎ≥¥
 */
export async function refreshKakaoToken(refreshToken: string): Promise<Record<string, any>> {
  try {
    const config = getKakaoConfig();
    const clientId = config.clientId;
    
    if (!clientId) {
      throw new Error('Kakao Client IDÍ∞Ä ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
    }
    
    const response = await fetch('https://kauth.kakao.com/oauth/token', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
      },
      body: new URLSearchParams({
        grant_type: 'refresh_token',
        client_id: clientId,
        refresh_token: refreshToken
      })
    });
    
    if (!response.ok) {
      throw new Error(`ÌÜ†ÌÅ∞ Í∞±Ïã† Ïã§Ìå®: ${response.status} ${response.statusText}`);
    }
    
    const tokenData = await response.json();
    return tokenData;
  } catch (error) {
    console.error('Kakao ÌÜ†ÌÅ∞ Í∞±Ïã† Ïã§Ìå®:', error);
    throw new SocialAuthError(
      SocialAuthErrorCode.TOKEN_REFRESH_FAILED, 
      'Ïπ¥Ïπ¥Ïò§ ÌÜ†ÌÅ∞ Í∞±Ïã†Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.',
      'kakao',
      error
    );
  }
} 